.footer-copyright {
    background-color: #282c35;
    padding-top: 3px;
    padding-bottom: 3px;
}

.footer-copyright p {
    margin: 10px;
    color: #ccc;
}

.my-footer {
    padding-top: 30px;
    background-color: #2c3e50;
    color: white;
}

.my-footer ul {
    list-style-type: none;
    padding-left: 0;
}

.my-footer a {
    color: #d2d1d1;
    text-decoration: none;
}

.my-footer a:hover,
.my-footer a:focus {
    text-decoration: none;
    color: white;
}

.social-networks {
    text-align: center;
    padding-top: 30px;
    padding-bottom: 30px;
}

.social-networks a {
    font-size: 32px;
    margin-right: 5px;
    color: #f9f9f9;
    padding: 10px;
    transition: 0.2s;
}

.social-networks a:hover {
    text-decoration: none;
}

.social-networks .twitter:hover {
    color: #00aced;
}

.social-networks .facebook:hover {
    color: #0077e2;
}

.social-networks .google:hover {
    color: #ef1a1a;
}

.social-networks .github:hover {
    color: #080808;
}

.social-networks .instagram:hover {
    color: #dd2095;
}
